.Paper {
  position: relative;
  width: 100%;
  height: 100%;
  background: #363636;
}

.Paper canvas {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.Loader {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -14px;
  margin-left: -36px;
  z-index: 9999;
}



